.nav-item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  transition: width 0.5s ease;
  height: 2px;
  background-color: #ff0000;
}

.nav-item:hover::after {
  width: 100%;
}
.nav__category:checked ~ .nav_mobile {
  transform: translateX(100%);
  transition: transform ease 0.5s;
}
.nav__category:checked ~ .nav__overlay {
  display: block;
}

.bg-header {
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.7) 10%,
    rgba(0, 0, 0, 0)
  );
  transition: 0.3s ease-in-out;
}
.bg-black {
  transition: transform 0.3s ease-in-out;
}
@keyframes transform {
  from {
    height: 0;
  }
  to {
    height: 100%;
  }
}

.icon--social {
  fill: #d1d5db;
}
.ovl--footer::after {
  content: "";
  left: 0;
  top: 0;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
}

.containerSlider {
  display: block;
}
.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}
.overlay-8::before {
  background: linear-gradient(
    to bottom,
    rgba(0, 47, 75, 0.5) 0%,
    rgba(220, 66, 37, 0.5) 100%
  );
}
.overlay::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.swiper-slide {
  flex-shrink: 0;
}
.bg-secondary {
  background-color: #636e72;
}
.shadow-card {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.get-in {
  animation: example 0.5s ease-in-out;
}
.get-in--text-1 {
  animation: example1 0.5s ease;
}
.get-in--text-2 {
  animation: example1 0.8s ease;
}
.get-in--btn {
  animation: example1 1s ease;
}
@keyframes example1 {
  from {
    transform: translateX(30%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes example {
  from {
    transform: translateX(-30%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.preload-body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  position: absolute;
  z-index: 2;
  background-color: #222;
}
#preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#loader {
  display: block;
  position: relative;
  left: 50%;
  top: 50%;
  width: 150px;
  height: 150px;
  margin: -75px 0 0 -75px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #9370db;
  animation: spin 2s linear infinite;
}
#loader:before {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #ba55d3;
  animation: spin 3s linear infinite;
}
#loader:after {
  content: "";
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #ff00ff;
  animation: spin 1.5s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.movie-card {
  position: relative;
  background-position: top;
  padding-top: 14%;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.stroke-solid {
  stroke-dashoffset: 0;
  stroke-dasharray: 300;
  stroke-width: 4px;
  transition: stroke-dashoffset 1s ease, opacity 1s ease;
}
.icon {
  transform: scale(0.8);
  transform-origin: 50% 50%;
  transition: transform 200ms ease-out;
}

#play:hover .icon {
  transform: scale(0.9);
}

#play:hover .stroke-solid {
  opacity: 1;
  stroke-dashoffset: 300;
}

#play {
  cursor: pointer;
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  visibility: hidden;
}
.bg-playbtn:hover #play {
  visibility: visible;
}
.bg-playbtn:hover .scale-img {
  transform: scale(1.5);
}
.bg-playbtn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #7f8c8d;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease-in-out;
}
.bg-playbtn {
  padding: 4.5rem;
}
.bg-playbtn:hover::after {
  visibility: visible;
  opacity: 0.5;
}


